@font-face {
  font-family: poppins;
  src: url(fonts/GoogleSans-Regular.ttf);
}
@font-face {
  font-family: poppins-medium;
  src: url(fonts/GoogleSans-Medium.ttf);
}
@font-face {
  font-family: poppins-bold;
  src: url(fonts/GoogleSans-Bold.ttf);
}
@font-face {
  font-family: poppins-black;
  src: url(fonts/GoogleSans-Bold.ttf);
}
:root {
  --primary-color: #29d882;
  --secondary-color: #baf3d7;
  --text-primary: #090909;
  --text-secondary: #88898b;
  --text-tertiary: #b6b6b6;
  --primary-color-hover: #64f1ad;

  --bg-primary: #fff;
  --bg-secondary: #f6f7f9;

  --box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.1), 0 4px 4px rgba(0, 0, 0, 0.1);
  --nav-size: 240px;
  --border: 1px solid #f0f1f2;
  --border-dark: 1px solid gainsboro;
  --border-dashed: 1px dashed #c0c0c0;
  --border-radius: 3px;
  --speed: 250ms;

  --green: rgb(0, 177, 0);
  --blue: rgb(49, 60, 214);
  --yellow: rgb(241, 238, 30);
  --red: rgb(241, 44, 30);
  --orange: rgb(241, 114, 30);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  outline: none;
  font-family: poppins;
  /* scroll-behavior: smooth; */
}

::-webkit-scrollbar {
  height: 5px;
  width: 5px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  width: 10px;
  background: #a6acb4;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
